@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

@layer base {
    body {
        font-family: sans-serif, "Noto Sans";
    }
}

@layer components {
    @responsive{
        .input-style {
            @apply block px-3 py-2 mt-1 w-full bg-white rounded-md border-2 border-gray-300 shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500;
        }
    }
}

.headroom {
    will-change: transform;
    transition: transform 200ms linear;
}

.headroom--pinned {
    transform: translateY(0%);
}

.headroom--unpinned {
    transform: translateY(-100%);
}

/* width */
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #1E293B;
}

::-webkit-scrollbar-track:horizontal {
    background: #fff;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #D1D5DB;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb:horizontal {
    background: #D1D5DB;
    border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #6B7280;
}

::-webkit-scrollbar-thumb:hover:horizontal {
    background: #6B7280;
}

.fade-enter-active,
.fade-leave-active {
    transition: all 0.5s;
}

.fade-enter-from,
.fade-leave-to {
    opacity: 0;
}

.fade-enter-to,
.fade-leave-from {
    opacity: 1;
}


.list-enter-active,
.list-leave-active,
.list-move {
    transition: all 0.2s;
}

.list-enter-from {
    opacity: 0;
    transform: translateY(-20px);
}

.list-leave-to {
    opacity: 0;
    transform: translateY(-20px);
}

.slide-leave-active,
.slide-enter-active {
    transition: all 0.3s;
}

.slide-enter-from,
.slide-leave-to {
    transform: translateX(-100%);
}

.ck-editor__editable_inline {
    /* 設定最低高度 */
    min-height: 500px;
}
